    height: 422px;
    margin-inline: auto;
    position: relative;
  }

  .gallery-img {
    height: 422px;
    border-radius: 24px;
  }
}

#slideset-gallery > div {
  opacity: 0;
  width: 100%;
  animation: 24s linear infinite autoplay-gallery;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes autoplay-gallery {
  0% {
    opacity: 0;
  }

  4% {
    opacity: 1;
  }

  33.33% {
    opacity: 1;
  }

  37.33% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

#slideset-gallery > :nth-child(1) {
  animation-delay: 0s;
}

#slideset-gallery > :nth-child(2) {
  animation-delay: 8s;
}

#slideset-gallery > :nth-child(3) {
  animation-delay: 16s;
}

